<template>
    <div>
      <div ref="randomBox" :style="{ backgroundColor: backgroundColor }">
        <RandomBoxDetail v-on:color="colorChange" v-bind:propsdata="mycolor"></RandomBoxDetail>
      </div>
    </div>
  </template>
  
  <script>
  import RandomBoxDetail from '@/components/RandomBox/RandomBoxDetail'
  
  export default {
    name: "randomBox",
    components: {
      RandomBoxDetail
    },
    data() {
      return {
        mycolor: '#' + Math.round(Math.random() * 0xffffff).toString(16),
        play: ''
      }
    },
    created() {
  
    },
    mounted() {
      this.colorChange();
    },
    methods: {
      colorChange() {
        this.$data.mycolor = '#' + Math.round(Math.random() * 0xffffff).toString(16);
        console.info('parent_colorChange', this.$data.mycolor);
        console.info('>', this.$refs.randomBox.style.backgroundColor);
      },
    },
    computed: {
      backgroundColor() {
        return this.$data.mycolor;
      }
    }
  }
  </script>
  


