<template>
  <div class="box">
    <h1>{{ mycolor }}</h1>
    <button @click="colorChange">play</button>
  </div>
</template>
<script>
export default {
  props: ['propsdata'],
  data() {
    return {
      mycolor: this.propsdata
    }
  },
  methods: {
    colorChange() {
      this.$emit('color');
    }
  },
  watch: {
    propsdata: {
      immediate: true,
      handler(newVal, oldVal) {
        this.mycolor = newVal;
      }
    }
  },
}
</script>

  
<style scoped>
button {
  border: none;
  font-size: 20px;
  padding: 1rem 3rem;
  cursor: pointer;
  text-transform: uppercase;
}

.box {
  max-height: 300px;
  display: flex;
  justify-content: center;
  font-size: 50px;
  align-items: center;
  color: #000000;
  flex-direction: column;
}
</style>


